import Modal from "../../components/Modal/Modal"
import React, { useState, useCallback } from "react"
import Settings from "../Settings/Settings"
import Controls from "../Controls/Controls"
import ResultSet from "../ResultSet/ResultSet"
import seedCalculator from "./SeedCalculator"
import Info from "../../components/Info/Info"
import GearDatabaseList from "../GearDatabaseList/GearDatabaseList"
import lang from "../../assets/json/language/EUen.json"

const useLocalStorage = (storageKey, fallbackState) => {
    const [value, _setValue] = useState(JSON.parse(localStorage.getItem(storageKey)) ?? fallbackState)

    const setValue = useCallback(
        value => {
            _setValue(value)
            localStorage.setItem(storageKey, JSON.stringify(value))
        },
        [storageKey]
    )

    return [value, setValue]
}

const SeedChecker = props => {
    const [gearList, setGearList] = useLocalStorage("gearList", {
        HaveGearHeadMap: {},
        HaveGearClothesMap: {},
        HaveGearShoesMap: {},
    })
    const [loading, setLoading] = useState(false)
    const [currentBrand, setCurrentBrand] = useState({ value: "B99", label: "amiibo" })
    const [currentDrink, setCurrentDrink] = useState({ value: "None", label: "None" })

    const [currentSeed, setCurrentSeed] = useState("0x1")
    const [currentStartFrame, setCurrentStartFrame] = useState(0)
    const [currentNumberOfFrames, setCurrentNumberOfFrames] = useState(10)
    const [displayResults, setDisplayResults] = useState([])
    const [currentGear, setCurrentGear] = useState({ Label: "None" })

    const brandChangeHandler = event => {
        setCurrentBrand(event)
    }

    const drinkChangeHandler = event => {
        setCurrentDrink(event)
    }

    const checkSeedHandler = event => {
        let inputstr = event.target.value.toUpperCase()
        if (inputstr.substring(0, 2) === "0X") {
            inputstr = inputstr.substring(2)
        }
        inputstr = inputstr.replace(/[^0-9A-F]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0) {
            inputstr = "0"
        }
        inputstr = "0x" + inputstr
        inputstr = inputstr.substring(0, 10) // cut off too many characters
        setCurrentSeed(inputstr)
    }

    const checkStartFrameHandler = event => {
        let inputstr = event.target.value
        let negative = inputstr.substring(0, 1) === "-"
        inputstr = inputstr.replace(/[^0-9]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0) {
            inputstr = "0"
        } else {
            if (negative) {
                inputstr = "-" + inputstr
            }
        }
        inputstr = inputstr.substring(0, 5) // cut off too many characters
        setCurrentStartFrame(inputstr)
    }

    const checkNumberOfFrameHandler = event => {
        let inputstr = event.target.value
        inputstr = inputstr.replace(/[^0-9]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0) {
            inputstr = "0"
        }
        inputstr = inputstr.substring(0, 5) // cut off too many characters
        setCurrentNumberOfFrames(inputstr)
    }

    const calculateResultHandler = () => {
        setLoading(false)
        setDisplayResults(
            seedCalculator(
                currentSeed,
                parseInt(currentStartFrame),
                parseInt(currentNumberOfFrames),
                currentBrand.value,
                currentDrink.value
            )
        )
    }

    const updateSeedHandler = (newSeed, storeRes) => {
        setCurrentSeed(newSeed)
        if (storeRes) {
            const original = { ...currentGear.OriginalEntry }
            original.RandomContext = parseInt(newSeed.substring(2), 16)
            const gearKey =
                currentGear.Type === 0
                    ? "HaveGearHeadMap"
                    : currentGear.Type === 1
                    ? "HaveGearClothesMap"
                    : "HaveGearShoesMap"
            const new_dict = { ...gearList }
            new_dict[gearKey][currentGear.Id] = original
            setGearList(new_dict)
        }
    }

    const gearChangeHandler = newGear => {
        setCurrentBrand({
            value: newGear.value.Brand,
            label: lang["CommonMsg/Gear/GearBrandName"][newGear.value.Brand],
        })
        if (newGear.Seed > 0) {
            setCurrentSeed(`0x${(newGear.Seed >>> 0).toString(16)}`)
        }
        setCurrentGear(newGear)
    }

    const filterFunction = val => {
        return val.RandomContext > 0
    }

    return (
        <>
            <Modal show={loading} />
            <h1>Splatoon 3 - Gear Seed Checker</h1>
            <Info />
            <GearDatabaseList
                currentGear={currentGear}
                gearChange={gearChangeHandler}
                filterFunction={filterFunction}
            />
            <Settings
                currentBrand={currentBrand}
                brandChange={brandChangeHandler}
                currentDrink={currentDrink}
                drinkChange={drinkChangeHandler}
            />
            <Controls
                seedValidator={checkSeedHandler}
                currentSeed={currentSeed}
                currentStartFrame={currentStartFrame}
                currentNumberOfFrames={currentNumberOfFrames}
                startValidator={checkStartFrameHandler}
                numberValidator={checkNumberOfFrameHandler}
                calculateResults={calculateResultHandler}
            />
            <ResultSet results={displayResults} updateSeed={updateSeedHandler} currentGear={currentGear} />
        </>
    )
}

export default SeedChecker
